import React, { useState } from 'react'
import _ from 'lodash'
import { Link } from 'react-router-dom'


import './App.css'
import { alternateCount, countSums } from './utils'

function App() {
  const nums = _.range(2, 13)
  const [selected, setSelected] = useState([])

  const toggleBox = (n) => {
    let newSelected
    if (selected.includes(n)) {
      newSelected = _.without(selected, n)
    } else {
      newSelected = [...selected, n]
    }
    setSelected(newSelected)
  }

  const p = alternateCount(selected)
  const altP = countSums(selected)
  if (p !== altP) {
    console.log('Functions do not match!')
  }

  const showOptions = selected.length === 2
  const unselected = nums.filter((n) => !selected.includes(n))

  const toPercent = (p) => {
    return `${(100 * p).toString().slice(0, 4)}%`
  }

  return (
    <div className="container">
      <div className="my-4">
          <h1>Don't Stop Tool</h1>
      </div>
      <div className="row my-2">
        <div className="col">
          <button
            onClick={() => {
              setSelected([])
            }}
            className="btn btn-primary"
          >
            Reset
          </button>
        </div>
      </div>

      <div className="row text-center mb-3">
        {nums.map((i) => {
          let optionProb
          if (showOptions && unselected.includes(i)) {
            optionProb = alternateCount([...selected, i])
            console.log(optionProb)
          }
          return (
            <div
              onClick={() => {
                toggleBox(i)
              }}
              key={`num-${i}`}
              className={`col-3 my-4`}
            >
              <div className={`${selected.includes(i) ? 'selected' : ''} p-3 number my-auto`}>
                <div className="row text-center">
                  <div className="col">{i}</div>
                </div>
                <div className="row text-center">
                  <div className={`col ${optionProb ? '' : 'white'}`}>
                    {optionProb ? toPercent(optionProb) : '---'}
                  </div>
                </div>
              </div>
            </div>
          )
        })}
      </div>
      <div className="row">
        <div className="col">
          <h3>Probability: {`${(100 * p).toString().slice(0, 4)}%`}</h3>
        </div>
      </div>
    </div>
  )
}

export default App
